<template>
<div>
  <v-app-bar
    app
    id="home-app-bar"
    elevation="1"
    elevate-on-scroll
    height="80"
    light
  >
    

    <v-container class="mx-auto py-0">
      <v-row align="center">
        <h1
          class="mr-5 indigo--text text--darken-1"
          contain
          height="48"
          width="48"
          max-width="48"
          @click="$vuetify.goTo(0)"
          
        >Kaori Sushi and Sake Bar</h1>
        
        <v-spacer />

        <div>
          <v-tabs
            class="hidden-sm-and-down"
            optional
            background-color="transparent"
          >
            <v-tab
              v-for="(name, i) in items"
              :key="i"
              :to="{ name }"
              :exact="name === 'Home'"
              :ripple="false"
              class="font-weight-bold indigo--text text--darken-1"
              min-width="96"
              text
            >
              {{ name }}
            </v-tab>
            <v-btn class="mt-1 ml-2" color="red white--text">Order Online</v-btn>
          </v-tabs>
        </div>
      
        <v-app-bar-nav-icon
            class="hidden-md-and-up"
            @click="drawer = !drawer"
        />
      </v-row>
    </v-container>
  </v-app-bar>
  <v-navigation-drawer
        v-model="drawer"
        app
        dark
        temporary
        width='80%'
    >
        <v-container>
            <v-list align="center">
                <v-list-item
                    v-for="(name, i) in items"
                    :key="i"
                    :to="{ name }"
                    :exact="name === 'Home'"
                    :ripple="false"
                    class="font-weight-bold"
                  >
                    <v-list-item-title 
                      v-text="name" />
                </v-list-item>
            </v-list>
        </v-container>
      </v-navigation-drawer>
    </div>

</template>

<script>
import {
    mapGetters,
  } from 'vuex';

export default {
    name: 'Navbar',
    data: () => ({
        selection: [],
        drawer: false,
        items: [
            'Home',
            'Contact',
            'Menu',
        ],
        computed: {
            ...mapGetters(['links']),
        },
        methods: {
            onClick (e, item) {
                e.stopPropagation()

                if (item.to === '/') {
                this.$vuetify.goTo(0)
                this.setDrawer(false)
                console.log(this.$vuetify.application)

                return
                }

                if (item.to || !item.href) return
                this.$vuetify.goTo(item.href)
                console.log(this.$vuetify.application)
                this.setDrawer(false)
                

                if (item.to || !item.href) return
                this.$vuetify.goTo(item.href.endsWith('!') ? 0 : item.href)
            }

        }
    })
}
</script>

