import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    items: [
      {
        text: 'Home',
        href: '#!',
      },
      {
        text: 'Contact',
        href: '#Contact',
      },
      {
        text: 'Menu',
        href: '#Menu',
      }
    ],
    drawer: true,
  },
  getters: {
    links: (state) => {
      return state.items.href
    },
  },
  mutations: {
    setDrawer: (state, payload) => (state.drawer = payload),
    toggleDrawer: (state) => (state.drawer = !state.drawer)
  },
  actions: {
  }
})
